import Vue from 'vue'
import Router from 'vue-router'

import Home from './components/Home.vue'
import Projects from './components/Projects.vue'
import Travel from './components/Travel.vue'
import TrackThat from './components/TrackThat.vue'
import ClimateMap from './components/ClimateMap.vue'
import Hell from './components/Hell.vue'
import EpidemicsChart from './components/EpidemicsChart.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,

  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/projects',
      name: 'Projects',
      component: Projects
    },
    {
      path: '/travel',
      name: 'Travel',
      component: Travel
    },
    {
      path: '/trackthat',
      name: 'TrackThat',
      component: TrackThat
    },
    {
      path: '/climatemap',
      name: 'ClimateMap',
      component: ClimateMap
    },
    {
      path: '/hell',
      name: 'Hell',
      component: Hell
    },
    {
      path: '/epidemicschart',
      name: 'EpidemicsChart',
      component: EpidemicsChart
    }
  ]
})
